body {
  background-color: black;
  width: auto;
  height: auto;
}

ul {
  list-style: none;
}

ul .pic-list {
  display: inline-block;
}

.hero {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 80px;
  width: auto;
}

.current {
  color: green;
}

.polaroid a {
  background: #ffffff;
  display: inline-block;
  margin: 55px 75px 30px;
  padding: 15px 15px 30px;
  text-align: center;
  text-decoration: none;
  -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
  -moz-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
  box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
  -webkit-transition: all .20s linear;
  -moz-transition: all .20s linear;
  transition: all .20s linear;
  z-index: 0;
  position: relative;
}

.polaroid a:after {
  color: #333;
  font-size: 25px;
  content: attr(title);
  position: relative;
  top: 15px;
}

.polaroid img {
  display: block;
  width: 250px;
}

.polaroid a:hover {
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  transform: scale(1.2);
  z-index: 10;
  -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, .7);
  -moz-box-shadow: 0 10px 20px rgba(0, 0, 0, .7);
  box-shadow: 0 10px 20px rgba(0, 0, 0, .7);
}





.wrapper {
  width: 60%;
  text-align: center;
  margin-top: 100px;
}

.home-polaroid {
  background: #fff;
  padding: 1rem;
  -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
  -moz-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
  box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
}

.home-polaroid>img {
  max-width: 90%;
  height: auto;
}

.caption {
  font-size: 1.8rem;
  text-align: center;
  line-height: 2em;
}

.item .home-polaroid:before {
  content: '';
  position: absolute;
  z-index: -1;
  transition: all 0.40s;
}

.item:nth-of-type(2n+1) {
  transform: scale(0.8, 0.8) rotate(5deg);
  transition: all 0.35s;
}

.item:nth-of-type(2n+1) .home-polaroid:before {
  transform: rotate(6deg);
  height: 20%;
  width: 47%;
  bottom: 30px;
  right: 12px;
  box-shadow: 0 2.1rem 2rem rgba(0, 0, 0, 0.4);
}

.item:nth-of-type(2n+2) {
  transform: scale(0.8, 0.8) rotate(-5deg);
  transition: all 0.35s;
}

.item:nth-of-type(2n+2) .home-polaroid:before {
  transform: rotate(-6deg);
  height: 20%;
  width: 47%;
  bottom: 30px;
  left: 12px;
  box-shadow: 0 2.1rem 2rem rgba(0, 0, 0, 0.4);
}

.item:hover {
  filter: none;
  transform: scale(1, 1) rotate(0deg);
  transition: all 0.40s;
}

.item:hover .home-polaroid:before {
  content: '';
  position: absolute;
  z-index: -1;
  transform: rotate(0deg);
  height: 60%;
  width: 60%;
  bottom: 0%;
  right: 10%;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.2);
  transition: all 0.35s;
}

//navigation

.sidebar {
  position: fixed;
  width: 25%;
  height: 100vh;
  background: white;
  font-size: 0.65em;
  margin-left: 75%;
  top: 0%;
}

.nav {
  position: relative;
  margin: 0 15%;
  text-align: left;
  top: 50%;
  transform: translateY(-50%);
  font-weight: bold;
}

.nav .unlist-nav {
  list-style: none;
  
  .list-nav {
    position: relative;
    margin: 3.2em 0;
    
    .nav-link {
      line-height: 5em;
      text-transform: uppercase;
      text-decoration: none;
      letter-spacing: 0.4em;
      color: rgba(black, 0.50);
      display: block;
      transition: all ease-out 300ms;
    }
    
    &.current .nav-link {
      color: black;
    }
    
    &:not(.current)::after {
      opacity: 0.2;
    }
    
    &:not(.current):hover .nav-link {
      color: rgba(black, 1);
    }
    
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 0.2em;
      background: black;
      left: 0;
      bottom: 0;
      background-image: linear-gradient(to right, #5e42a6, #b74e91)
    }
  }
}
